<template>
  <div>
    <va-box
        title="Criar Projeto"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <b-field label="Id">
              <b-input disabled="disabled" v-model="project.id"></b-input>
            </b-field>

            <b-field label="Nome">
              <b-input v-model="project.name"></b-input>
            </b-field>

            <b-field label="Descrio">
              <b-input v-model="project.description"></b-input>
            </b-field>

            <b-field label="Dono">
              <b-select v-model="project.owner" @input="onChangePlans(project.owner)" placeholder="Selecione">
                <option
                  v-for="user in listUsers"
                  :value="user.id"
                  :key="user.id">
                  {{ user.fullName }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Selecione o mapa para add ao projeto">
              <b-select v-model="currentPlan" placeholder="Selecione">
                <option
                  v-for="plan in listPlans"
                  :value="plan.id"
                  :key="plan.id">
                  {{ plan.name }}
                </option>
              </b-select>
              <div class="btn_button_addPlan" @click="addPlan()">Adicionar mapa</div>
            </b-field>

            <b-field label="Mapas relacionados ao projeto">
              <ul>
              <li
                v-for="plan in project.plans"
                  :value="plan.id"
                  :key="plan.id">
                  {{ plan.name }}
              </li>
              </ul>
            </b-field>

          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="submitProject()">Salvar</button>
        </div>

    </va-box>
  </div>
</template>

<script>

import VABox from '../../../widgets/VABox'
import VAInput from '../../../components/VAInput'
// import VAInputGroup from '../../../components/VAInputGroup'
// import VATextareaGroup from '../../../components/VATextareaGroup'
import VAButton from '../../../components/VAButton'
// import VAFormGroup from '../../../components/VAFormGroup'
import VASelect from '../../../components/VASelect'

export default {
  name: 'create-project',
  props: ['id'],
  data () {
    return {
      project: {
        id: '',
        name: '',
        description: '',
        owner: '',
        plans: []
      },
      currentPlan: '',
      listUsers: [],
      listPlans: []
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })
    if (this.id != null) {
      this.$store.dispatch('getProject', this.id)
      .then(async (project) => {
        await this.onChangePlans(project.owner.id)
        this.project = project
        this.project.owner = project.owner.id
      })
    }
  },
  created () {
  },
  methods: {
    onChangePlans: function (owner) {
      this.$store.dispatch('getPlans', 'owner=' + owner + '&project=null')
      .then(() => {
        this.listPlans = this.$store.getters.getPlans
      })
    },
    addPlan: function () {
      var index = this.listPlans.findIndex(plan => plan.id === this.currentPlan)
      this.project.plans.push(this.listPlans[index])
      this.listPlans.splice(index, 1)
    },
    submitProject () {
      if (this.id == null) {
        this.$store.dispatch('postProject', this.project)
        .then(() => {
          this.$toast.open({
            message: 'Projeto cadastrado!',
            type: 'is-success'
          })
        })
      } else {
        this.$store.dispatch('editProject', this.project)
        .then(() => {
          this.$toast.open({
            message: 'Projeto editado!',
            type: 'is-success'
          })
        })
      }
    }
  },

  components: {
    'va-box': VABox,
    // 'va-input-group': VAInputGroup,
    // 'va-checkbox': VACheckBox,
    'va-input': VAInput,
    'va-button': VAButton,
    // 'va-textarea-group': VATextareaGroup
    // 'va-form-group': VAFormGroup,
    // 'va-radio': VARadio,
    'va-select': VASelect
  }
}
</script>

<style>
  .btn_button_salve{
    background-color: #00c1ed;
    border-color: #00acd6;
    border-radius: 3px;

    border: 1px solid transparent;
    width: 100%;
    color: #fff;

    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
  }
  .btn_button_addPlan{
    cursor: pointer;
    background-color: #3399ff;
    border-color: #00acd6;
    border-radius: 3px;

    border: 1px solid transparent;
    color: #fff;
    margin-left: 20px;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
  }
</style>



