<template>
  <div>
    <va-box
        title="Criar Orthomosaico"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <b-field label="ID">
              <b-input disabled="disabled" v-model="orthomosaic.id"></b-input>
            </b-field>

            <b-field label="Usurio">
              <b-select v-model="orthomosaic.owner" @input="onChangePlans(orthomosaic.owner)" placeholder="Selecione">
                <option
                  v-for="user in listUsers"
                  :value="user.id"
                  :key="user.id">
                  {{ user.fullName }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Mapa">
              <b-select v-model="orthomosaic.plan" placeholder="Selecione">
                <option
                  v-for="plan in listPlans"
                  :value="plan.id"
                  :key="plan.id">
                  {{ plan.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Imagem">
              <b-select v-model="orthomosaic.image" placeholder="Selecione">
                <option
                  v-for="image in listImagens"
                  :value="image.id"
                  :key="image.id">
                  {{ image.id }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Tipo">
              <b-select v-model="orthomosaic.image" placeholder="Selecione">
                <option
                  v-for="typeL in listTypes"
                  :value="typeL.id"
                  :key="typeL.id">
                  {{ typeL.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Tiles URL">
              <b-input disabled="disabled" v-model="orthomosaic.tiles_url"></b-input>
            </b-field>

            <file-upload
              @fileSuccess="(p) => successEvent(p.file, p.response)"
              @queueComplete="(p) => vqueueComplete(p.file, p.xhr, p.formData)"
              uploadUrl="https://www.googleapis.com/upload/storage/v1/b/storage.vantum.com.br/o?uploadType=multipart&name=orthomosaics/"
              :modelId="orthomosaic.id"
              ref=fileUpload>
            </file-upload>

            <b-field label="Imagem">
              <b-input disabled="disabled" v-model="orthomosaic.image"></b-input>
            </b-field>

          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="validateBeforeSubmitProject()">Salvar</button>
        </div>

      </va-box>
  </div>
</template>

<script>
import VABox from '../../../widgets/VABox'

import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import axios from 'axios'
import FileUpload from '../../../components/FileUpload'

export default {
  name: 'list-orthomosaics',
  data () {
    return {
      orthomosaic: {
        id: null,
        tiles_url: '',
        type: '',
        plan: null,
        image: null,
        task: null
      },
      user: '',
      listUsers: [],
      listPlans: [],
      listImagens: [],
      listTypes: [{id: '1', name: 'Rural'}, {id: '2', name: 'Urbano'}]
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })
    // if (this.id != null) {
    //   this.$store.dispatch('getAlbum', this.id)
    //   .then((album) => {
    //     this.album = album
    //   })
    // }
  },
  created () {

  },
  methods: {

    onChangeProject: function (user) {
      // console.log(user)
      this.$store.dispatch('getPlan', 'owner=' + user)
      .then(() => {
        this.listPlans = this.$store.getters.getPlans
      })
      this.$store.dispatch('getImagens', 'owner=' + user)
      .then(() => {
        this.listImages = this.$store.getters.getImages
      })
    },
    successEvent (file, response) {
      var image = {
        filename: file.upload.filename,
        size: file.size,
        width: file.width,
        height: file.height,
        type: file.type,
        url: response.mediaLink
      }

      axios.post('/api/v2/image', image)
      .then(response => {
        this.updateOrthomosaic(this.orthomosaic.id, response.data.id)
      })
      .catch(e => {
        console.log(e)
      })
    },
    vqueueComplete (file, xhr, formData, orthomosaic) {
      this.queueComplete = true
      this.$toast.open({
        message: 'Upload completo',
        type: 'is-success'
        // position: 'is-bottom'
      })

      var task = {
        job: "['tile-orthomosaic']",
        process_status: 'waiting',
        project: this.orthomosaic.project,
        orthomosaic: this.orthomosaic.id
      }

      this.$store.dispatch('postTask', task).then((response) => {
        this.$toast.open({
          message: 'Tarefa criada com sucesso',
          type: 'is-success'
        })
      })
      .catch(e => {
        this.$toast.open({
          message: 'Erro ao criar a tarefa',
          type: 'is-danger'
        })
      })
    },

    updateOrthomosaic: function (id, image) {
      return new Promise((resolve, reject) => {
        var orthomosaic = {
          image: image
        }
        // trocar pelos mtodos criados
        this.$store.dispatch('putOrthomosaic', orthomosaic).then((response) => {
          this.orthomosaic = response.data
          resolve(response.data)
          this.$toast.open({
            message: 'Orthomocaic criado com sucesso',
            type: 'is-success'
          })
        })
        .catch(e => {
          this.$toast.open({
            message: 'Erro na criao do orthomosaico',
            type: 'is-danger'
          })
        })
      })
    },
    async validateBeforeSubmitProject () {
      if (this.$refs.fileUpload.getQueuedFiles() > 0) {
        console.log(this.orthomosaic)

        await this.$store.dispatch('postOrthomosaic', this.orthomosaic).then((response) => {
          console.log('ooooooo', response)
          this.orthomosaic = response
        })
        this.$refs.fileUpload.processQueue()

        this.$toast.open({
          message: 'Upload em andamento',
          type: 'is-success'
          // position: 'is-bottom'
        })
      } else {
        this.$toast.open({
          message: 'Ortomosaico no adicionado',
          type: 'is-danger'
          // position: 'is-bottom'
        })
      }
    }
  },
  components: {
    'va-box': VABox,
    'file-upload': FileUpload
  }
}
</script>

<style>
</style>



