
import {services} from './../api'

const state = {
  sharing: [],
  currentSharing: null
}

const getters = {
  getSharings: state => state.sharing,
  getCurrentSharing: state => state.currentSharing
}

const mutations = {
  post_sharing (state, sharing) { // Ajustar
    state.sharing.push(sharing)
  },
  get_sharings (state, sharing) {
    // console.log('========', sharing)
    state.sharing = sharing
  },
  get_sharing (state, sharing) {
    state.currentSharing = sharing
  },
  edit_sharing (state, sharing) {
    var index = state.sharing.findIndex(sharing => sharing.id === sharing.id)
    state.sharing[index] = sharing
    // state.sharing.splice(index, 1)
    // state.sharing.push(Sharing)
  },
  delete_sharing (state, currentSharing) {
    var index = state.sharing.findIndex(sharing => sharing.id === currentSharing.id)
    state.sharing.splice(index, 1)
  }
}

const actions = {
  postSharing ({
    commit,
    state
  }, sharing) {
    return services.sharing.post(sharing)
      .then((response) => {
        console.log(response)
        commit('post_sharing', response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getSharings ({
    commit,
    state
  }, filter) {
    return services.sharing.getAll(filter)
      .then((response) => {
        commit('get_sharings', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getSharing ({
    commit,
    state
  }, id) {
    return services.sharing.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },
  editSharing ({
    commit,
    state
  }, id) {
    return services.sharing.edit(id)
      .then((response) => {
        console.log(response)
        commit('edit_sharing', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  deleteSharing ({
    commit,
    state
  }, id) {
    return services.sharing.delete(id)
      .then((response) => {
        commit('delete_sharing', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



