import {
  services
} from './../api'

const state = {
  tasks: [],
  currentTask: null
}

const getters = {
  getTasks: state => state.tasks,
  getCurrentTask: state => state.currentTask
}

const mutations = {
  post_task (state, task) { // Ajustar
    // console.log('++++---', task)
    state.tasks.push(task)
  },
  get_run (state, task) {
    console.log(task)
  },
  get_tasks (state, tasks) {
    state.tasks = tasks
  },
  get_task (state, task) {
    state.currentTask = task
  },
  edit_task (state, task) {
    var index = state.tasks.findIndex(task => task.id === tasks.id)
    state.tasks[index] = task
  },
  delete_task (state, currentTask) {
    var index = state.tasks.findIndex(task => task.id === currentTask.id)
    state.tasks.splice(index, 1)
  }
}

const actions = {
  postTask ({
    commit,
    state
  }, task) {
    return services.tasks.post(task)
      .then((response) => {
        commit('post_task', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getRun ({
    commit,
    state
  }, task) {
    return services.tasks.run(task)
      .then((response) => {
        commit('get_run', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  postRun ({
    commit,
    state
  }, obj) {
    return services.tasks.run2(obj.task, obj.instance)
      .then((response) => {
        console.log(response)
        // commit('get_run', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getTasks ({
    commit,
    state
  }, filter) {
    return services.tasks.getAll(filter)
      .then((response) => {
        commit('get_tasks', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getTask ({
    commit,
    state
  }, id) {
    return services.tasks.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },
  editTask ({
    commit,
    state
  }, id) {
    return services.tasks.edit(id)
      .then((response) => {
        console.log(response)
        commit('edit_task', responde.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  deleteTask ({
    commit,
    state
  }, id) {
    return services.tasks.delete(id)
      .then((response) => {
        console.log(response)
        commit('delete_task', response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



