<template>
  <div>
    <va-box
        title="Criar Mapa"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <b-field label="Id">
              <b-input disabled="disabled" v-model="plan.id"></b-input>
            </b-field>

            <b-field label="Nome">
              <b-input v-model="plan.name"></b-input>
            </b-field>

            <b-field label="Localizao">
              <b-input v-model="plan.location"></b-input>
            </b-field>

            <b-field label="Dono">
              <b-select v-model="plan.owner" @input="onChangeProject(plan.owner)" placeholder="Selecione">
                <option
                  v-for="user in listUsers"
                  :value="user.id"
                  :key="user.id">
                  {{ user.fullName }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Album">
              <b-select v-model="plan.album" placeholder="Selecione">
                <option
                  v-for="album in listAlbums"
                  :value="album.id"
                  :key="album.id">
                  {{ album.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Orthomosaic">
              <b-select v-model="plan.orthomosaic" placeholder="Selecione">
                <option
                  v-for="orthomosaic in listOrthomosaics"
                  :value="orthomosaic.id"
                  :key="orthomosaic.id">
                  {{ orthomosaic.id }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Projetos">
              <b-select v-model="plan.orthomosaic" placeholder="Selecione">
                <option
                  v-for="project in listProjects"
                  :value="project.id"
                  :key="project.id">
                  {{ project.id }}
                </option>
              </b-select>
            </b-field>

            <div class="p_one">
              <div class="section_title">
                <div class="title">
                  Geometria
                </div>
                <div class="add_icon" @click="addGeometryPlan()">
                  <i class="fas fa-plus-circle"></i>
                </div>
              </div>

              <div v-for="geometry in plan.geometrys">
                <row>
                  <div class="col-xs-2">
                    <b-field label="" >
                        Longitude<b-input class="field_geometry"  v-model="geometry.longitude"></b-input>
                      </b-field>
                    </div>
                  <div class="col-xs-2">
                    <b-field label="" >
                        Latitude<b-input class="field_geometry"  v-model="geometry.latitude"></b-input>
                      </b-field>
                  </div>
                </row>
              </div>
            </div>

            <div class="p_one">
              <div class="section_title">
                <div class="title">
                  Annotation
                </div>
                <div class="add_icon" @click="addAnnotation()">
                  <i class="fas fa-plus-circle"></i>
                </div>
              </div>

              <div v-for="annotation in plan.annotations">
                <row>
                  <div class="col-xs-2">
                    <b-field label="Tipo de anotao">
                      <b-select v-model="annotation.type" placeholder="Selecione">
                        <option
                          v-for="typeAnn in listTypeAnnotations"
                          :value="typeAnn.id"
                          :key="typeAnn.id">
                          {{ typeAnn.name}}
                        </option>
                      </b-select>
                    </b-field>
                  </div>

                  <div class="col-xs-2">
                    <b-field label="">
                      Descrio<b-input class="field_geometry" v-model="annotation.description"></b-input>
                    </b-field>
                  </div>
                  <div class="col-xs-2">
                   <b-field label="">
                      Cor<b-input class="field_geometry" v-model="annotation.color"></b-input>
                    </b-field>
                  </div>
                </row>
                <row>
                  <div class="col-xs-10 col-xs-offset-2">
                    <div class="col-xs-12 subsection_title">
                      <div class="title">
                        Geometria
                      </div>
                      <div class="add_icon" @click="addGeometryAnnotation(annotation)">
                        <i class="fas fa-plus-circle"></i>
                      </div>
                    </div>

                    <div v-for="geometry in annotation.geometrys">
                      <row>
                        <div class="col-xs-6">
                          <b-field label="" >
                              Longitude<b-input class="field_geometry"  v-model="geometry.longitude"></b-input>
                            </b-field>
                          </div>
                        <div class="col-xs-6">
                          <b-field label="" >
                              Latitude<b-input class="field_geometry"  v-model="geometry.latitude"></b-input>
                            </b-field>
                        </div>
                      </row>
                    </div>
                  </div>


                </row>
              </div>

              <!--b-field label="Lista de geometrias">
                <ul>
                  <li
                    v-for="annotation in listAnnotations"
                      :value="annotation.id"
                      :key="annotation.id">
                    desc.: {{ annotation.description }} color: {{ annotation.color}}
                  </li>
                </ul>
              </b-field-->

            </div>

          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="submitProject()">Salvar</button>
        </div>

    </va-box>
  </div>
</template>

<script>

import VABox from '../../../widgets/VABox'
import VAInput from '../../../components/VAInput'
// import VAInputGroup from '../../../components/VAInputGroup'
// import VATextareaGroup from '../../../components/VATextareaGroup'
import VAButton from '../../../components/VAButton'
// import VAFormGroup from '../../../components/VAFormGroup'
import VASelect from '../../../components/VASelect'

export default {
  name: 'create-plan',
  props: ['id'],
  data () {
    return {
      plan: {
        id: null,
        name: '',
        location: '',
        owner: null,
        album: null,
        geometrys: [],
        annotations: [],
        orthomosaic: '',
        project: null
      },
      listUsers: [],
      listAlbums: [],
      listOrthomosaics: [],
      listProjects: [],
      listTypeColors: [{id: '1', name: 'Azul'}],
      listTypeAnnotations: [{id: '1', name: 'Ponto'}, {id: '2', name: 'Linha'}, {id: '3', name: 'Poligono'}]
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })
    if (this.id != null) {
      this.$store.dispatch('getPlan', this.id)
      .then((plan) => {
        this.plan = plan
      })
    }
  },
  created () {
  },
  methods: {
    onChangeProject: function (user) {
      this.$store.dispatch('getAlbums', user.id)
      .then(() => {
        this.listAlbums = this.$store.getters.getAlbums
      })
      this.$store.dispatch('getOrthomosaics', user.id)
      .then(() => {
        this.listOrthomosaics = this.$store.getters.getOrthomosaics
      })
      this.$store.dispatch('getProjects', user.id)
      .then(() => {
        this.listProjects = this.$store.getters.getProjects
      })
    },
    addGeometryPlan: function () {
      this.plan.geometrys.push({
        id: null,
        latitude: '',
        longitude: ''
      })
      // this.project.plans.push(this.listPlans[index])
    },

    addGeometryAnnotation: function (a) {
      a.geometrys.push({
        id: null,
        latitude: '',
        longitude: ''
      })
    },

    addAnnotation: function () {
      this.plan.annotations.push({
        id: null,
        description: '',
        color: '',
        geometrys: []
      })
    },

    submitProject () {
      if (this.id == null) {
        this.$store.dispatch('postPlan', this.plan)
        .then(() => {
          this.$toast.open({
            message: 'Mapa cadastrado!',
            type: 'is-success'
          })
        })
      } else {
        this.$store.dispatch('editPlan', this.plan)
        .then(() => {
          this.$toast.open({
            message: 'Mapa editado!',
            type: 'is-success'
          })
        })
      }
    }
  },

  components: {
    'va-box': VABox,
    // 'va-input-group': VAInputGroup,
    // 'va-checkbox': VACheckBox,
    'va-input': VAInput,
    'va-button': VAButton,
    // 'va-textarea-group': VATextareaGroup
    // 'va-form-group': VAFormGroup,
    // 'va-radio': VARadio,
    'va-select': VASelect
  }
}
</script>

<style lang="scss">
  .btn_button_salve{
    background-color: #00c0ef;
    border-color: #00acd6;
    border-radius: 3px;

    border: 1px solid transparent;
    width: 100%;
    color: #fff;

    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
  }
  .btn_button_addPlan{
    cursor: pointer;
    background-color: #3399ff;
    border-color: #00acd6;
    border-radius: 3px;

    border: 1px solid transparent;
    color: #fff;
    margin-left: 20px;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
  }
  .field_geometry{
    border: 1px solid transparent;
    font-weight: 40;
    line-height: 1;

    &.right {
      margin-left: 20px;
    }
  }
  .p_one {
    font-weight: bold;
    border-style: dotted;
    border-width: 1px;
    margin-bottom: 5px;
  }
  .subsection_title {
    color: #363636;
    padding: .2em .6em .3em;
    display:inline-table;

    .title {
      font-size: 14px;
      display: inline-block;
    }

    .add_icon {
      margin-left: 10px;
      color: green;
      display: inline-block;
    }
  }
  .section_title {
    color: #363636;
    padding: .2em .6em .3em;
    display:inline-table;

    .title {
      font-size: 20px;
      display: inline-block;
    }

    .add_icon {
      margin-left: 10px;
      color: green;
      display: inline-block;
    }
  }
</style>



