const state = {
  projects: [],
  currentProject: null
}

const getters = {
  getProjects: state => state.projects,
  getCurrentProject: state => state.currentProject
}

const mutations = {
  post_project (state, project) { // Ajustar
    state.projects.push(project)
  },
  get_projects (state, projects) {
    state.projects = projects
  },
  get_project (state, project) {
    state.currentProject = project
  },
  edit_project (state, project) {
    var index = state.projects.findIndex(project => project.id === projects.id)
    state.projects[index] = project
    // state.projects.splice(index, 1)
    // state.projects.push(project)
  },
  delete_project (state, currentProject) {
    var index = state.projects.findIndex(project => project.id === currentProject.id)
    state.projects.splice(index, 1)
  }
}

const actions = {
  postProject ({
    commit,
    state
  }, project) {
    return services.projects.post(project)
      .then((response) => {
        commit('post_project', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getProjects ({
    commit,
    state
  }, filter) {
    return services.projects.getAll(filter)
      .then((response) => {
        // console.log(response)
        commit('get_projects', response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getProject ({
    commit,
    state
  }, id) {
    // console.log('++++', id)
    return services.projects.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },

  getProjectOwner ({
    commit,
    state
  }, id) {
    return services.projects.getOwner(id)
      .then((response) => {
        // console.log('AQUI', response)
        commit('get_projects', response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  editProject ({
    commit,
    state
  }, project) {
    return services.projects.edit(project)
      .then((response) => {
        console.log(response)
        commit('edit_project', responde.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  deleteProject ({
    commit,
    state
  }, id) {
    return services.projects.delete(id)
      .then((response) => {
        console.log(response)
        commit('delete_project', response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



