<template>
  <section class="content">

    <div class="row center-block">
      <h2>Listar Compartilhamento</h2>
      <div class="col-md-12">
        <div class="box">
          <!--div class="box-header">
            <h3 class="box-title">Data Table With Full Features</h3>
          </div-->
          <!-- /.box-header -->
          <div class="box-body">
            <div class="dataTables_wrapper form-inline dt-bootstrap" id="example1_wrapper">
              <div class="row">
                <div class="col-sm-6">
                  <div id="example1_length" class="dataTables_length">

                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-sm-12 table-responsive">
                  <table aria-describedby="example1_info" role="grid" id="example1" class="table table-bordered table-striped dataTable">
                    <thead>
                      <tr role="row">
                        <th aria-label="" aria-sort="ascending" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting_asc">ID</th>
                        <th aria-label="" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Dono</th>
                        <th aria-label="" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Projeto</th>
                        <th aria-label="" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Mapa</th>
                        <th aria-label="" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Album</th>
                        <th aria-label="" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Usurio</th>
                        <th aria-label="" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Nvel</th>
                        <th aria-label="" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Publico</th>
                        <th aria-label="" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Ao</th>
                      </tr>
                    </thead>
                    <tbody>
                        <tr v-for="sharing in sharings">
                          <td>{{ sharing.id }}</td>
                          <td>{{ sharing.owner.fullName }}</td>
                          <td>
                            <div v-if="sharing.project != null">
                              {{ sharing.project.name }}
                            </div>
                          </td>
                          <td>
                            <div v-if="sharing.plan != null">
                              {{ sharing.plan.name }}
                            </div>
                          </td>
                          <td>
                            <div v-if="sharing.album != null">
                              {{ sharing.album.name }}
                            </div>
                          </td>
                          <td>{{ sharing.user.fullName }}</td>
                          <td>{{ sharing.level }}</td>
                          <td>{{ sharing.linkSharing }}</td>
                          <td>
                            <div class="deleteButton" v-on:click.stop="confirmCustomDelete(sharing)">
                              <i class="fa fa-trash"></i>
                            </div>
                            <div class="editButton" v-on:click.stop="editSharing(sharing)">
                              <i class="fa fa-edit"></i>
                            </div>
                          </td>
                        </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <th colspan="1" rowspan="1">ID</th>
                        <th colspan="1" rowspan="1">Dono</th>
                        <th colspan="1" rowspan="1">Projeto</th>
                        <th colspan="1" rowspan="1">Nvel</th>
                        <th colspan="1" rowspan="1">Usurio</th>
                        <th colspan="1" rowspan="1">Ao</th>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            </div>
            <!-- /.box-body -->
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery'
// Require needed datatables modules
require('datatables.net')
require('datatables.net-bs')

export default {
  name: 'Tables',
  data () {
    return {
      sharings: []
    }
  },
  mounted () {
    this.$store.dispatch('getSharings')
    .then(() => {
      this.sharings = this.$store.getters.getSharings
      this.$nextTick(() => {
        $('#example1').DataTable()
      })
    })
  },
  methods: {
    deleteSharing (idSharing) {
      this.$store.dispatch('deleteSharing', idSharing)
      .then(() => {
        this.$toast.open({
          message: 'Compartilhamento deletado!',
          type: 'is-success'
        })
      })
    },

    editSharing (sharing) {
      this.$router.push({name: 'EditSharing', params: {id: sharing.id}})
    },

    confirmCustomDelete (sharing) {
      this.$dialog.confirm({
        title: 'Deletando Compartilhamento',
        message: 'Voc tem certeza que deseja <b>deletar</b> o compartilhamento ? Isso  uma ao sem volta.',
        confirmText: 'Deletar compartilhamento',
        type: 'is-danger',
        hasIcon: true,
        onConfirm: () => this.deleteSharing(sharing.id)
      })
    }
  }
}
</script>

<style>
/* Using the bootstrap style, but overriding the font to not draw in
   the Glyphicons Halflings font as an additional requirement for sorting icons.

   An alternative to the solution active below is to use the jquery style
   which uses images, but the color on the images does not match adminlte.

@import url('/static/js/plugins/datatables/jquery.dataTables.min.css');
*/

@import url('/static/js/plugins/datatables/dataTables.bootstrap.css');

table.dataTable thead .sorting:after,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_desc:after {
  font-family: 'FontAwesome';
}

table.dataTable thead .sorting:after {
  content: '\f0dc';
}

table.dataTable thead .sorting_asc:after {
  content: '\f0dd';
}

table.dataTable thead .sorting_desc:after {
  content: '\f0de';
}
</style>



