import {services} from './../api'

const state = {
  plans: [],
  currentPlan: null
}

const getters = {
  getPlans: state => state.plans,
  getCurrentPlan: state => state.currentPlan
}

const mutations = {
  post_plan (state, plans) { // Ajustar
    state.plans.push(plans)
  },
  get_plans (state, plans) {
    state.plans = plans
  },
  get_plan (state, plan) {
    state.currentPlan = plan
  },
  get_plan_project (state, plan) {
    state.plans = plan
  },
  edit_plan (state, currentPlan) {
    var index = state.plans.findIndex(plan => plan.id === currentPlan.id)
    state.plans[index] = currentPlan
    // state.plans.splice(index, 1)
    // state.plans.push(Plan)
  },

  delete_plan (state, currentPlan) {
    var index = state.plans.findIndex(plan => plan.id === currentPlan.id)
    state.plans.splice(index, 1)
  }
}

const actions = {
  postPlan ({
    commit,
    state
  }, plan) {
    return services.plans.post(plan)
      .then((response) => {
        commit('post_plan', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getPlans ({
    commit,
    state
  }, filter) {
    return services.plans.getAll(filter)
      .then((response) => {
        commit('get_plans', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getPlan ({
    commit,
    state
  }, id) {
    return services.plans.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },

  editPlan ({
    commit,
    state
  }, plan) {
    return services.plans.edit(plan)
      .then((response) => {
        console.log(response)
        commit('edit_plan', responde.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },

  deletePlan ({
    commit,
    state
  }, id) {
    return services.plans.delete(id)
      .then((response) => {
        console.log(response)
        commit('delete_plan', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



