<template>
  <section class="content">

    <div class="row center-block">
      <h2>Lista de Albums</h2>
      <div class="col-md-12">
        <div class="box">
          <!--div class="box-header">
            <h3 class="box-title">Data Table With Full Features</h3>
          </div-->
          <!-- /.box-header -->
          <div class="box-body">
            <div class="dataTables_wrapper form-inline dt-bootstrap" id="example1_wrapper">
              <div class="row">
                <div class="col-sm-6">
                  <div id="example1_length" class="dataTables_length">

                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-sm-12 table-responsive">
                  <table aria-describedby="example1_info" role="grid" id="example1" class="table table-bordered table-striped dataTable">
                    <thead>
                      <tr role="row">
                        <th aria-label="" aria-sort="ascending" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting_asc">ID</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Nome</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Description</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Altitude</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Width</th>
                        <th aria-label="" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Height</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Tipo</th>
                        <th aria-label="" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Contador</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Dono</th>
                        <th aria-label="" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Ao</th>
                      </tr>
                    </thead>
                    <tbody>
                        <tr class="even" role="row" v-for="(album, index) in albums" :key="index">
                          <td>{{ album.id }}</td>
                          <td>{{ album.name }}</td>
                          <td>{{ album.description }}</td>
                          <td>{{ album.altitude }}</td>
                          <td>{{ album.width }}</td>
                          <td>{{ album.height }}</td>
                          <td>{{ album.type }}</td>
                          <td>{{ album.count }}</td>
                          <td>{{ album.owner.fullName }}</td>
                          <td>
                            <div class="deleteButton" v-on:click.stop="confirmCustomDelete(album)">
                              <i class="fa fa-trash"></i>
                            </div>
                            <div class="editButton" v-on:click.stop="editAlbum(album)">
                              <i class="fa fa-edit"></i>
                            </div>
                          </td>
                        </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <th colspan="1" rowspan="1">ID</th>
                        <th colspan="1" rowspan="1">Nome</th>
                        <th colspan="1" rowspan="1">Descrio</th>
                        <th colspan="1" rowspan="1">Altitude</th>
                        <th colspan="1" rowspan="1">Width</th>
                        <th colspan="1" rowspan="1">Height</th>
                        <th colspan="1" rowspan="1">type</th>
                        <th colspan="1" rowspan="1">Contador</th>
                        <th colspan="1" rowspan="1">Dono</th>
                        <th colspan="1" rowspan="1">Ao</th>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            </div>
            <!-- /.box-body -->
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery'
// Require needed datatables modules
require('datatables.net')
require('datatables.net-bs')

export default {
  name: 'Tables',
  data () {
    return {
      albums: []
    }
  },
  mounted () {
    this.$store.dispatch('getAlbums')
    .then(() => {
      this.albums = this.$store.getters.getAlbums

      this.$nextTick(() => {
        $('#example1').DataTable()
      })
    })
  },

  methods: {
    deleteAlbum (idAlbum) {
      this.$store.dispatch('deleteAlbum', idAlbum)
      .then(() => {
        this.$toast.open({
          message: 'Album deletado!',
          type: 'is-success'
        })
      })
      .catch(e => {
        console.log(e)
        this.$toast.open({
          message: 'No foi possvel deletar o Album!',
          type: 'is-danger'
        })
      })
    },

    editAlbum (album) {
      this.$router.push({name: 'EditAlbum', params: {id: album.id}})
      // console.log()
    },

    confirmCustomDelete (album) {
      this.$dialog.confirm({
        title: 'Deletando Projeto',
        message: 'Voc tem certeza que deseja <b>deletar</b> o album ? Isso  uma ao sem volta.',
        confirmText: 'Deletar album',
        type: 'is-danger',
        hasIcon: true,
        onConfirm: () => this.deleteAlbum(album.id)
      })
    }
  }
}
</script>

<style>
/* Using the bootstrap style, but overriding the font to not draw in
   the Glyphicons Halflings font as an additional requirement for sorting icons.

   An alternative to the solution active below is to use the jquery style
   which uses images, but the color on the images does not match adminlte.

@import url('/static/js/plugins/datatables/jquery.dataTables.min.css');
*/

@import url('/static/js/plugins/datatables/dataTables.bootstrap.css');

table.dataTable thead .sorting:after,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_desc:after {
  font-family: 'FontAwesome';
}

table.dataTable thead .sorting:after {
  content: '\f0dc';
}

table.dataTable thead .sorting_asc:after {
  content: '\f0dd';
}

table.dataTable thead .sorting_desc:after {
  content: '\f0de';
}
</style>



