<template>
  <div v-if="isHorizontal"
    class="form-group"
  >
    <label :for="vaId" class="col-sm-2 control-label">{{ title }}</label>
    <div class="col-sm-10">
      <select :value="value" @input="$emit('input', $event.target.value)" class="form-control" :disabled="isDisabled" :multiple="isMultiple">
        <option v-for="(item,index) in list" :data="item" :key="index" :value="item.value">
          {{ item.text }}
        </option>
      </select>
    </div>
  </div>
  <select :value="value" v-else
    class="form-control" :disabled="isDisabled" :multiple="isMultiple">
    <option v-for="(item,index) in list" :data="item" :key="index" :value="item.value">
      {{ item.text }}
    </option>
  </select>
</template>
<script>
export default {
  name: 'va-select',
  props: {
    value: {
      type: String
    },
    list: {
      type: Array,
      default: []
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    isMultiple: {
      type: Boolean,
      default: false
    },
    isHorizontal: {
      type: Boolean,
      default: false
    },
    title: {
      type: String
    },
    vaId: {
      type: String
    }
  },
  created () {

  }
}
</script>



