<template>

  <div>
    <va-box
        title="Criar Compartilhamento"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <b-field label="Id">
              <b-input disabled="disabled" v-model="sharing.id"></b-input>
            </b-field>

            <b-field label="Dono">
              <b-select v-model="sharing.owner" @input="onChangeUser(sharing.owner)" placeholder="Selecione">
                <option
                  v-for="user in listUsers"
                  :value="user.id"
                  :key="user.id">
                  {{ user.fullName }}
                </option>
              </b-select>
            </b-field>

             <b-field label="Tipo de Compartilhamento">
              <b-select v-model="typeSharing" placeholder="Selecione">
                <option
                  value='project'> Projeto
                </option>
                <option
                  value='plan'> Mapa
                </option>
                <option
                  value='album'> Album
                </option>
              </b-select>
            </b-field>

            <b-field label="Projetos" v-if="typeSharing == 'project'">
              <b-select v-model="sharing.project" placeholder="Selecione">
                <option
                  v-for="project in listProjects"
                  :value="project.id"
                  :key="project.id">
                  {{ project.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Mapas" v-if="typeSharing == 'plan'">
              <b-select v-model="sharing.plan" placeholder="Selecione">
                <option
                  v-for="plan in listPlans"
                  :value="plan.id"
                  :key="plan.id">
                  {{ plan.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Albums" v-if="typeSharing == 'album'">
              <b-select v-model="sharing.album" placeholder="Selecione">
                <option
                  v-for="album in listAlbums"
                  :value="album.id"
                  :key="album.id">
                  {{ album.id }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Usurio">
              <b-select v-model="sharing.user" placeholder="Selecione">
                <option
                  v-for="user in listUsers"
                  :value="user.id"
                  :key="user.id">
                  {{ user.fullName }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Nvel">
              <b-select v-model="sharing.level" placeholder="Selecione">
                <option
                  v-for="level in listLevels"
                  :value="level.id"
                  :key="level.id">
                  {{ level.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Publico">
              <b-checkbox v-model="sharing.linkSharing">Sim</b-checkbox>
            </b-field>

          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="submitSharing()">Salvar</button>
        </div>

    </va-box>
  </div>
</template>

<script>

import VABox from '../../../widgets/VABox'
import VAInput from '../../../components/VAInput'
// import VATextareaGroup from '../../../components/VATextareaGroup'
import VAButton from '../../../components/VAButton'

export default {
  name: 'created-sharing',
  props: ['id'],
  data () {
    return {
      selectedValue: 1,
      sharing: {
        id: '',
        level: '',
        linkSharing: false,
        owner: '',
        project: null,
        plan: null,
        album: null,
        user: ''
      },
      listUsers: [],
      listProjects: [],
      listAlbums: [],
      listPlans: [],
      listLevels: [{ id: '1', name: 'Vizualizar' }, {id: '2', name: 'Vizualizar + Editar'}, { id: '3', name: 'Vizualizar + Editar + Compartilhar' }],
      typeSharing: ''
    }
  },
  mounted () {
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })
    if (this.id != null) {
      this.$store.dispatch('getSharing', this.id)
      .then(async (sharing) => {
        // console.log(sharing)
        await this.onChangeUser(sharing.owner.id)
        this.sharing.id = sharing.id
        this.sharing.owner = sharing.owner.id

        if (sharing.project != null) {
          this.sharing.project = sharing.project.id
          this.typeSharing = 'project'
        } else if (sharing.plan != null) {
          this.sharing.plan = sharing.plan.id
          this.typeSharing = 'plan'
        } else {
          this.sharing.album = sharing.album.id
          this.typeSharing = 'album'
        }
        this.sharing.user = sharing.user.id
        this.sharing.level = sharing.level
        this.sharing.linkSharing = sharing.linkSharing
      })
    }
  },
  created () {
  },
  methods: {
    onChangeUser: function (user) {
      this.$store.dispatch('getProjects', 'owner=' + user)
        .then(() => {
          this.listProjects = this.$store.getters.getProjects
        })
      this.$store.dispatch('getPlans', 'owner=' + user)
        .then(() => {
          this.listPlans = this.$store.getters.getPlans
        })
      this.$store.dispatch('getAlbums', 'owner=' + user)
        .then(() => {
          this.listAlbums = this.$store.getters.getAlbums
        })
    },
    submitSharing () {
      if (this.id == null) {
        this.$store.dispatch('postSharing', this.sharing)
        .then(() => {
          this.$toast.open({
            message: 'Compartilhamento cadastrado!',
            type: 'is-success'
          })
        })
      } else {
        this.$store.dispatch('editSharing', this.sharing)
        .then(() => {
          this.$toast.open({
            message: 'Compartilhamento editado!',
            type: 'is-success'
          })
        })
      }
    }
  },
  components: {
    'va-box': VABox,
    // 'va-input-group': VAInputGroup,
    // 'va-checkbox': VACheckBox,
    'va-input': VAInput,
    'va-button': VAButton
    // 'va-textarea-group': VATextareaGroup
    // 'va-form-group': VAFormGroup,
    // 'va-radio': VARadio,
    // 'va-select': VASelect
  }
}
</script>

<style>

</style>



