<template>
  <section class="content">
    <div class="row center-block">
      <h2>Lista de Assuntos</h2>
      <div class="col-md-12">
        <div class="box">
          <!-- <div class="box-header">
            <h3 class="box-title">Data Table With Full Features</h3>
          </div> -->
          <!-- /.box-header -->
          <div class="box-body">
            <div class="dataTables_wrapper form-inline dt-bootstrap" id="example1_wrapper">
              <div class="row">
                <div class="col-sm-6">
                  <div id="example1_length" class="dataTables_length">

                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-sm-12 table-responsive">
                  <table aria-describedby="example1_info" role="grid" id="example1" class="table table-bordered table-striped dataTable">
                    <thead>
                      <tr role="row">
                        <th aria-label="Rendering engine: activate to sort column descending" aria-sort="ascending" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting_asc">ID</th>

                        <th aria-label="Status" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Status</th>

                        <th aria-label="Assunto" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Assunto</th>

                        <th aria-label="Mensagem" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Mensagem</th>

                        <th aria-label="Remetente" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Remetente</th>

                        <th aria-label="Destinatrio" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Destinatrio</th>

                        <th aria-label="Aes" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0">Aes</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr class="even" role="row" v-for="(email, index) in emails" :key="index">
                        <td class="sorting_1">{{ email.id }}</td>
                        <td>{{ email.status }}</td>
                        <td>{{ email.subject }}</td>
                        <td>{{ email.message }}</td>
                        <td>{{ email.from }}</td>
                        <td>{{ email.to }}</td>
                        <td>
                          <i class="fa fa-edit"></i>
                          <i class="fa fa-trash"></i>
                        </td>
                      </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <th colspan="1" rowspan="1">ID</th>
                        <th colspan="1" rowspan="1">Status</th>
                        <th colspan="1" rowspan="1">Assunto</th>
                        <th colspan="1" rowspan="1">Mensagem</th>
                        <th colspan="1" rowspan="1">Remetente</th>
                        <th colspan="1" rowspan="1">Destinatrio</th>
                        <th colspan="1" rowspan="1">Aes</th>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            </div>
            <!-- /.box-body -->
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery'
// Require needed datatables modules
require('datatables.net')
require('datatables.net-bs')

export default {
  name: 'list-tasks',
  data () {
    return {
      emails: []
    }
  },
  mounted () {
    this.$store.dispatch('getEmails')
    .then(() => {
      this.emails = this.$store.getters.getEmails

      this.$nextTick(() => {
        $('#example1').DataTable()
      })
    })
  }
}
</script>

<style>
/* Using the bootstrap style, but overriding the font to not draw in
   the Glyphicons Halflings font as an additional requirement for sorting icons.

   An alternative to the solution active below is to use the jquery style
   which uses images, but the color on the images does not match adminlte.

@import url('/static/js/plugins/datatables/jquery.dataTables.min.css');
*/

@import url('/static/js/plugins/datatables/dataTables.bootstrap.css');

table.dataTable thead .sorting:after,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_desc:after {
  font-family: 'FontAwesome';
}

table.dataTable thead .sorting:after {
  content: '\f0dc';
}

table.dataTable thead .sorting_asc:after {
  content: '\f0dd';
}

table.dataTable thead .sorting_desc:after {
  content: '\f0de';
}
</style>



