const state = {
  albums: [],
  currentAlbum: null
}

const getters = {
  getAlbums: state => state.albums,
  getCurrentAlbum: state => state.currentAlbum
}

const mutations = {
  post_album (state, albums) { // Ajustar
    state.albums.push(albums)
  },
  get_albums (state, albums) {
    state.albums = albums
  },
  get_album (state, album) {
    state.currentAlbum = album
  },
  get_album_project (state, album) {
    state.albums = album
  },
  edit_album (state, currentAlbum) {
    var index = state.albums.findIndex(album => album.id === currentAlbum.id)
    state.albums[index] = currentAlbum
    // state.albums.splice(index, 1)
    // state.albums.push(Album)
  },

  delete_album (state, currentAlbum) {
    var index = state.albums.findIndex(album => album.id === currentAlbum.id)
    state.albums.splice(index, 1)
  }
}

const actions = {
  postAlbum ({
    commit,
    state
  }, album) {
    return services.albums.post(album)
      .then((response) => {
        commit('post_album', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getAlbums ({
    commit,
    state
  }, filter) {
    return services.albums.getAll(filter)
      .then((response) => {
        commit('get_albums', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getAlbum ({
    commit,
    state
  }, id) {
    return services.albums.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },

  getAlbumProject ({
    commit,
    state
  }, project) {
    return services.albums.getAlbumProject(project)
      .then((response) => {
        console.log('console', response.data)
        commit('get_album_project', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  editAlbum ({
    commit,
    state
  }, album) {
    return services.albums.edit(album)
      .then((response) => {
        console.log(response)
        commit('edit_album', responde.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },

  deleteAlbum ({
    commit,
    state
  }, id) {
    return services.albums.delete(id)
      .then((response) => {
        console.log(response)
        commit('delete_album', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}



