<template>
  <div class="image-view">
    <!-- <div class="image">
      <img :src="image.url" alt="">
    </div> -->
    <div class="info">

      <b-field label="Core">
        <b-input v-model="instance.cores"></b-input>
      </b-field>

      <b-field label="Memria">
        <b-input v-model="instance.mem"></b-input>
      </b-field>

      <b-field label="Disco">
        <b-input v-model="instance.disk"></b-input>
      </b-field>

      <b-field label="Projeto">
        <b-select placeholder="Selecione" v-model="instance.project">
          <option
            v-for="project in listProjects"
            :value="project.name"
            :key="project.id">
            {{ project.name }}
          </option>
        </b-select>
      </b-field>

      <div slot="footer">
        <button class="btn_button_salve" @click="execTask()">Executar</button>
      </div>

    </div>
  </div>
</template>


<script>
  // import axios from 'axios'

export default {
    name: 'image-view',
    props: {
      task: {
        type: Object
      }
    },
    data () {
      return {
        instance: {
          id: null,
          cores: '',
          mem: '',
          disk: '',
          project: ''
        },
        form: {
          fullname: 'JUCALEO',
          email: 'leonardojoao01@hotmail.com',
          actuation: null,
          message: 'Teste de e-mail'
        },
        listZones: [{id: '1', name: 'southamerica-east1'}],
        listZonesB: [{id: '1', name: 'southamerica-east1-b'}],
        listProjects: [{id: '1', name: 'projeto-233719'}, {id: '2', name: 'fine-entry-221919'}],
        listImages: [{id: '1', name: 'image-vantum-14'}],
        listServiceAccount: [{id: '1', name: '1044341927719-compute@developer.gserviceaccount.com'}]
      }
    },
    methods: {
      execTask () {
        // console.log(this.task)
        // console.log('Instance', this.instance)
        this.$store.dispatch('postRun', {task: this.task, instance: this.instance})
        .then(() => {
          this.$toast.open({
            message: 'Tarefa executada!',
            type: 'is-success'
          })
        })
        .catch(() => {
          this.$toast.open({
            message: 'Tarefa no executada!',
            type: 'is-danger'
          })
        })
      }
    }
  }
</script>

<style lang="scss" scoped>
 .image-view{
   margin: auto;

    .image{

    }

    .info{
      position: relative;
      background-color: white;
      color: black;
      margin: 0;
      padding: 20px;
      text-align: center;

      .attribute{
        margin-bottom: 10px;

        .label{
          font-weight: bold;
          color: black;
          line-height: 10px;
          margin-bottom: 0;
        }

        .value{
          display: inline-block;
          color: black;
        }
      }
    }
 }
</style>



