<template>
  <div>
    <va-box
        title="Criar Album"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <b-field label="Id">
              <b-input disabled="disabled" v-model="album.id"></b-input>
            </b-field>

            <b-field label="Nome">
              <b-input v-model="album.name"></b-input>
            </b-field>

            <b-field label="Descrio">
              <b-input v-model="album.description"></b-input>
            </b-field>

            <b-field label="Altitude">
              <b-input disabled="disabled" v-model="album.altitude"></b-input>
            </b-field>

            <b-field label="Width">
              <b-input disabled="disabled" v-model="album.width"></b-input>
            </b-field>

            <b-field label="Height">
              <b-input disabled="disabled" v-model="album.height"></b-input>
            </b-field>

            <b-field label="Tipo">
              <b-select placeholder="Selecione" v-model="album.type">
                <option
                  v-for="typeL in listTypes"
                  :value="typeL.id"
                  :key="typeL.id">
                  {{ typeL.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Nmero de imagens">
              <b-input disabled="disabled" v-model="album.count"></b-input>
            </b-field>

            <b-field label="Usurio">
              <b-select placeholder="Selecione" v-model="album.owner">
                <option
                  v-for="user in listUsers"
                  :value="user.id"
                  :key="user.id">
                  {{ user.fullName }}
                </option>
              </b-select>
            </b-field>
          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="submitProject()">Salvar</button>
        </div>

    </va-box>
  </div>
</template>

<script>

import VABox from '../../../widgets/VABox'
import VAInput from '../../../components/VAInput'
// import VAInputGroup from '../../../components/VAInputGroup'
// import VATextareaGroup from '../../../components/VATextareaGroup'
import VAButton from '../../../components/VAButton'
// import VAFormGroup from '../../../components/VAFormGroup'
import VASelect from '../../../components/VASelect'

export default {
  name: 'create-album',
  props: ['id'],
  data () {
    return {
      album: {
        id: null,
        name: '',
        description: '',
        altitude: 0,
        width: 0,
        height: 0,
        type: '',
        count: 0,
        owner: null
      },
      // listProjects: [],
      listUsers: [],
      listTypes: [{id: '1', name: 'Rural'}, {id: '2', name: 'Urbano'}]
    }
  },
  mounted () {
    // this.$store.dispatch('getProjects')
    // .then(() => {
    //   this.listProjects = this.$store.getters.getProjects
    // })
    this.$store.dispatch('getUsers')
    .then(() => {
      this.listUsers = this.$store.getters.getUsers
    })
    if (this.id != null) {
      this.$store.dispatch('getAlbum', this.id)
      .then((album) => {
        this.album = album
        this.album.owner = album.owner.id
      })
    }
  },
  created () {
  },
  methods: {
    submitProject () {
      if (this.id == null) {
        this.$store.dispatch('postAlbum', this.album)
        .then(() => {
          this.$toast.open({
            message: 'Album cadastrado!',
            type: 'is-success'
          })
        })
      } else {
        this.$store.dispatch('editAlbum', this.album)
        .then(() => {
          this.$toast.open({
            message: 'Album editado!',
            type: 'is-success'
          })
        })
      }
    }
  },

  components: {
    'va-box': VABox,
    // 'va-input-group': VAInputGroup,
    // 'va-checkbox': VACheckBox,
    'va-input': VAInput,
    'va-button': VAButton,
    // 'va-textarea-group': VATextareaGroup
    // 'va-form-group': VAFormGroup,
    // 'va-radio': VARadio,
    'va-select': VASelect
  }
}
</script>

<style>
  .btn_button_salve{
    background-color: #00c0ef;
    border-color: #00acd6;
    border-radius: 3px;

    border: 1px solid transparent;
    width: 100%;
    color: #fff;

    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 1.42857143;
  }
</style>



