<template>
  <section class="content">

    <div class="row center-block">
      <h2>Lista de Mapas</h2>
      <div class="col-md-12">
        <div class="box">
          <!--div class="box-header">
            <h3 class="box-title">Data Table With Full Features</h3>
          </div-->
          <!-- /.box-header -->
          <div class="box-body">
            <div class="dataTables_wrapper form-inline dt-bootstrap" id="example1_wrapper">
              <div class="row">
                <div class="col-sm-6">
                  <div id="example1_length" class="dataTables_length">

                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-sm-12 table-responsive">
                  <table aria-describedby="example1_info" role="grid" id="example1" class="table table-bordered table-striped dataTable">
                    <thead>
                      <tr role="row">
                        <th aria-label="" aria-sort="ascending" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting_asc">ID</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Nome</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Lugar</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Dono</th>
                        <th aria-label="" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Album</th>
                        <th aria-label="" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Projeto</th>
                        <th aria-label="Aes" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0">Aes</th>
                      </tr>
                    </thead>
                    <tbody>
                        <tr class="even" role="row" v-for="(plan, index) in plans" :key="index">
                          <td>{{ plan.id }}</td>
                          <td>{{ plan.name }}</td>
                          <td>{{ plan.location }}</td>
                          <td>{{ plan.owner.fullName }}</td>
                          <td>{{ plan.album }}</td>
                          <!--td>
                            <div v-if="plan.geometrys != null">
                                <div v-for="(geometry, index) in plan.geometrys" :key="index">
                                  {{ geometry.latitude }},
                                  {{ geometry.longitude }}
                                </div>
                            </div>
                          </td-->
                          <td>
                            <div v-if="plan.project != null">
                              {{ plan.project.name }}
                            </div>
                          </td>

                          <td>
                            <div class="deleteButton" v-on:click.stop="confirmCustomDelete(plan)">
                              <i class="fa fa-trash"></i>
                            </div>
                            <div class="editButton" v-on:click.stop="editPlan(plan)">
                              <i class="fa fa-edit"></i>
                            </div>
                          </td>
                        </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <th colspan="1" rowspan="1">ID</th>
                        <th colspan="1" rowspan="1">Nome</th>
                        <th colspan="1" rowspan="1">Localizao</th>
                        <th colspan="1" rowspan="1">Dono</th>
                        <th colspan="1" rowspan="1">Album</th>
                        <th colspan="1" rowspan="1">Orthomosaics</th>
                        <th colspan="1" rowspan="1">Projetos</th>
                        <th colspan="1" rowspan="1">Geometria</th>
                        <th colspan="1" rowspan="1">Ao</th>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            </div>
            <!-- /.box-body -->
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery'
// Require needed datatables modules
require('datatables.net')
require('datatables.net-bs')

export default {
  name: 'Tables',
  data () {
    return {
      plans: []
    }
  },
  mounted () {
    this.$store.dispatch('getPlans')
    .then(() => {
      this.plans = this.$store.getters.getPlans
      console.log(this.plans)

      this.$nextTick(() => {
        $('#example1').DataTable()
      })
    })
  },

  methods: {
    deletePlan (idPlan) {
      this.$store.dispatch('deletePlan', idPlan)
      .then(() => {
        this.$toast.open({
          message: 'Mapa deletado!',
          type: 'is-success'
        })
      })
      .catch(e => {
        console.log(e)
        this.$toast.open({
          message: 'No foi possvel deletar o Album!',
          type: 'is-danger'
        })
      })
    },

    editPlan (plan) {
      this.$router.push({name: 'EditPlan', params: {id: plan.id}})
      // console.log()
    },

    confirmCustomDelete (plan) {
      this.$dialog.confirm({
        title: 'Deletando Mapa',
        message: 'Voc tem certeza que deseja <b>deletar</b> o mapa ? Isso  uma ao sem volta.',
        confirmText: 'Deletar mapa',
        type: 'is-danger',
        hasIcon: true,
        onConfirm: () => this.deletePlan(plan.id)
      })
    }
  }
}
</script>

<style>
/* Using the bootstrap style, but overriding the font to not draw in
   the Glyphicons Halflings font as an additional requirement for sorting icons.

   An alternative to the solution active below is to use the jquery style
   which uses images, but the color on the images does not match adminlte.

@import url('/static/js/plugins/datatables/jquery.dataTables.min.css');
*/

@import url('/static/js/plugins/datatables/dataTables.bootstrap.css');

table.dataTable thead .sorting:after,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_desc:after {
  font-family: 'FontAwesome';
}

table.dataTable thead .sorting:after {
  content: '\f0dc';
}

table.dataTable thead .sorting_asc:after {
  content: '\f0dd';
}

table.dataTable thead .sorting_desc:after {
  content: '\f0de';
}
</style>



