<template>
  <div>
    <va-box
        title="Criar Tarefa"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <div id="app">
              <div id="modal" @click="showModal">New Post</div>
            </div>

            <div id="app">
              <div id="modal" @click="showModal2">New Post2</div>
            </div>

            <b-field label="ID">
              <b-input disabled="disabled" v-model="task.id"></b-input>
            </b-field>

            <b-field label="Status de Processamento">
              <b-input disabled="disabled" v-model="task.process_status"></b-input>
            </b-field>

            <b-field label="Tarefa">
              <b-checkbox v-for="job in listJobs" v-model="task.job"
                :native-value="job.id">
                {{job.name}}
              </b-checkbox>
            </b-field>

            <b-field label="Incio">
              <b-input disabled="disabled" v-model="task.start_time"></b-input>
            </b-field>

            <b-field label="Fim">
              <b-input disabled="disabled" v-model="task.end_time"></b-input>
            </b-field>

            <b-field label="Resultado">
              <b-input disabled="disabled" v-model="task.output"></b-input>
            </b-field>

            <b-field label="Usurio">
              <b-select v-model="user" @input="onChangeProject(user)" placeholder="Selecione o dono do projeto">
                <option
                  v-for="users in listUsers"
                  :value="users.id"
                  :key="users.id">
                  {{ users.fullName }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Projetos">
              <b-select v-model="project" @input="onChangeAlbum(project)" placeholder="Selecione o projeto">
                <option
                  v-for="project in listProjects"
                  :value="project.id"
                  :key="project.id">
                  {{ project.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Mapa">
              <b-select v-model="task.plan" placeholder="Selecione o mapa">
                <option
                  v-for="plan in listPlans"
                  :value="plan.id"
                  :key="plan.id">
                  {{ plan.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Ortomosaico">
              <b-input disabled="disabled" v-model="task.ortomosaico"></b-input>
            </b-field>

          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="submitTask()">Salvar</button>
        </div>

      </va-box>
  </div>
</template>

<script>
import VABox from '../../../widgets/VABox'
import VAInput from '../../../components/VAInput'
import VAButton from '../../../components/VAButton'

export default {
  name: 'list-tasks',
  props: ['id'],
  // components: {
  //   TaskView
  // },
  data () {
    return {
      checkedCategories: [],
      checkboxGroup: [],
      task: {
        id: null,
        createdAt: '',
        updatedAt: '',
        process_status: '',
        job: [],
        start_time: '',
        end_time: '',
        output: '',
        // project: null,
        album: null,
        plan: null,
        orthomosaic: null
      },
      project: '',
      user: '',
      listUsers: [],
      listProjects: [],
      listAlbums: [],
      listPlans: [],
      listJobs: [
        {
          id: 'orthomosaic',
          name: 'Orthophoto'
        },
        {
          id: 'tiles',
          name: 'Tiles'
        }
      ],
      auxProject: null
    }
  },
  mounted () {
    // this.$store.dispatch('getProjects')
    // .then(() => {
    //   this.listProjects = this.$store.getters.getProjects
    // })
    this.$store.dispatch('getUsers')
    .then(() => {
      console.log(this)
      this.listUsers = this.$store.getters.getUsers
    })
    if (this.id != null) {
      this.$store.dispatch('getTask', this.id)
      .then((task) => {
        this.$store.dispatch('getAlbumProject', task.project)
        .then(() => {
          this.listAlbums = this.$store.getters.getAlbums
          this.task = task
        })
      })
    }
  },
  created () {

  },
  methods: {
    onChangeProject: function (user) {
      this.$store.dispatch('getProjects', 'owner=' + user)
      .then(() => {
        this.listProjects = this.$store.getters.getProjects
      })
    },
    onChangeAlbum: function (project) {
      this.$store.dispatch('getProject', project)
      .then((project) => {
        project.plans.forEach((plan) => {
          this.listPlans.push(plan)
        })
      })
    },
    check: function (e) {
      if (e.target.checked) {
        console.log(e.target.value)
      }
    },
    submitTask () {
      if (this.id == null) {
        this.$store.dispatch('postTask', this.task)
        .then(() => {
          this.$toast.open({
            message: 'Tarefa cadastrada!',
            type: 'is-success'
          })
        })
      } else {
        this.$store.dispatch('editTask', this.task)
        .then(() => {
          this.$toast.open({
            message: 'Tarefa editada!',
            type: 'is-success'
          })
        })
      }
    }
  },
  components: {
    'va-box': VABox,
    'va-input': VAInput,
    'va-button': VAButton
  }
}
</script>

<style>

</style>



