<template>
  <div>
    <va-box
        title="Criar Usurio"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <b-field label="Id">
              <b-input disabled="disabled" v-model="user.id"></b-input>
            </b-field>

            <b-field label="Nome">
              <b-input v-model="user.name"></b-input>
            </b-field>

            <b-field label="Email">
              <b-input v-model="user.email"></b-input>
            </b-field>

            <b-field label="Administrador">
              <b-select v-model="user.isSuperAdmin" placeholder="Selecione o status do email">
                <option
                  v-for="status in admStatus"
                  :value="status.id"
                  :key="status.id">
                  {{ status.name }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Email status">
              <b-select v-model="user.emailStatus" placeholder="Selecione o status do email">
                <option
                  v-for="status in emailStatus"
                  :value="status.id"
                  :key="status.id">
                  {{ status.id }}
                </option>
              </b-select>
            </b-field>

            <b-field label="Visto por ltimo">
              <b-input disabled="disabled" v-model="user.lastSeenAt"></b-input>
            </b-field>

            <b-field label="Password Reset Token">
              <b-input disabled="disabled" v-model="user.passwordResetToken"></b-input>
            </b-field>

            <b-field label="Password Reset Token Expiration">
              <b-input disabled="disabled" v-model="user.passwordResetTokenExpiresAt"></b-input>
            </b-field>

            <b-field label="Email Proof Token">
              <b-input disabled="disabled" v-model="user.emailProofToken"></b-input>
            </b-field>

            <b-field label="Email Proof Token Expiration">
              <b-input disabled="disabled" v-model="user.emailProofTokenExpiresAt"></b-input>
            </b-field>

            <b-field label="Email Change Candidate">
              <b-input disabled="disabled" v-model="user.emailChangeCandidate"></b-input>
            </b-field>

          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="submitUser()">Salvar</button>
        </div>

      </va-box>
  </div>
</template>

<script>
import VABox from '../../../widgets/VABox'
// import VAInputGroup from '../../../components/VAInputGroup'
// import VACheckBox from '../../../components/VACheckBox'
import VAInput from '../../../components/VAInput'
import VAButton from '../../../components/VAButton'
// import VATextareaGroup from '../../../components/VATextareaGroup'
// import VAFormGroup from '../../../components/VAFormGroup'
// import VARadio from '../../../components/VARadio'
// import VASelect from '../../../components/VASelect'

export default {
  name: 'create-user',
  data () {
    return {
      user: {
        id: '5c4f44757b13365266b20191',
        createdAt: 1548698741469,
        updatedAt: 1548900775968,
        email: 'betinhoh@gmail.com',
        name: 'Huberto Kaiser Filho',
        firstName: 'Huberto',
        lastName: 'Kaiser Filho',
        // password: '$2a$10$Ap0MOj94zn4v0omL9skIeu1egBsn6c0lhlBoPztWarLnNZUf2m3se',
        isSuperAdmin: true,
        passwordResetToken: '',
        passwordResetTokenExpiresAt: 0,
        emailProofToken: '',
        emailProofTokenExpiresAt: 0,
        emailStatus: 'confirmed',
        emailChangeCandidate: '',
        tosAcceptedByIp: '127.0.0.1',
        lastSeenAt: 1548900775968,
        stripeCustomerId: '',
        hasBillingCard: false,
        billingCardBrand: '',
        billingCardLast4: '',
        billingCardExpMonth: '',
        billingCardExpYear: ''
      },
      emailStatus: [{id: 'unconfirmed', name: 'No Confirmado'}, {id: 'confirmed', name: 'Confirmado'}],
      admStatus: [{id: true, name: 'Sim'}, {id: false, name: 'No'}]
    }
  },
  created () {

  },
  methods: {
    submitUser () {
      this.$store.dispatch('postUser', this.user)
      .then(() => {
        this.$toast.open({
          message: 'Usurio cadastrado!',
          type: 'is-success'
        })
      })
    }
  },

  components: {
    'va-box': VABox,
    // 'va-input-group': VAInputGroup,
    // 'va-checkbox': VACheckBox,
    'va-input': VAInput,
    'va-button': VAButton
    // 'va-textarea-group': VATextareaGroup,
    // 'va-form-group': VAFormGroup,
    // 'va-radio': VARadio,
    // 'va-select': VASelect
  }
}
</script>

<style>

</style>



