<template>
  <div>
    <va-box
        title="Criar Imagem"
        theme="box-info"
        :isBorder="true"
        widgetType="">

        <div slot="content">
          <form class="form-horizontal">

            <b-field label="Id">
              <b-input disabled="disabled" v-model="image.id"></b-input>
            </b-field>

            <b-field label="Name">
              <b-input disabled="disabled" v-model="image.filename"></b-input>
            </b-field>

            <b-field label="Size">
              <b-input disabled="disabled" v-model="image.size"></b-input>
            </b-field>

            <b-field label="Width">
              <b-input disabled="disabled" v-model="image.width"></b-input>
            </b-field>

            <b-field label="heught">
              <b-input disabled="disabled" v-model="image.height"></b-input>
            </b-field>

            <b-field label="Type">
              <b-input disabled="disabled" v-model="image.type"></b-input>
            </b-field>

            <b-field label="Album">
              <b-select placeholder="Selecione"  v-model="image.album">
                <option
                  v-for="album in listAlbums"
                  :value="album.id"
                  :key="album.id">
                  {{ album.id }}
                </option>
              </b-select>
            </b-field>

            <file-upload
              @fileSuccess="(p) => successEvent(p.file, p.response)"
              @queueComplete="(p) => vqueueComplete(p.file, p.xhr, p.formData)"
              :uploadUrl="get_upload_url"
              :modelId="image.album"
              ref=fileUpload>
            </file-upload>

          </form>

        </div>

        <div slot="footer">
          <button class="btn_button_salve" @click="validateBeforeSubmitImage()">Salvar</button>
        </div>

      </va-box>
  </div>
</template>

<script>
import VAInput from '../../../components/VAInput'
import VAButton from '../../../components/VAButton'

import VABox from '../../../widgets/VABox'

import 'vue2-dropzone/dist/vue2Dropzone.min.css'
import axios from 'axios'
import FileUpload from '../../../components/FileUpload'

export default {
  name: 'create-images',
  data () {
    return {
      image: {
        id: '5c525bfa54f95d52e60389c4',
        createdAt: 1548901370782,
        updatedAt: 1548901370782,
        filename: '14803030_1449640078384413_722532931_o.jpg',
        size: '136946',
        width: '960',
        height: '1280',
        type: 'image/jpeg',
        url: '',
        album: null
      },
      listAlbums: []
    }
  },
  mounted () {
    this.$store.dispatch('getAlbums')
    .then(() => {
      this.listAlbums = this.$store.getters.getAlbums
      // console.log(this.listAlbums)
    })
  },
  created () {

  },
  components: {
    'va-box': VABox,
    'va-input': VAInput,
    'va-button': VAButton,
    'file-upload': FileUpload
  },

  computed: {
    get_upload_url () {
      if (this.image.album != null) {
        var index = this.listAlbums.findIndex(album => album.id === this.image.album)
        var album = this.listAlbums[index]
        console.log(album.owner.id)
        console.log(album.id)
        return 'https://www.googleapis.com/upload/storage/v1/b/storage.vantum.com.br/o?uploadType=multipart&name=user_data/' + album.owner.email + '_' + album.owner.id + '/albums/'
      }
      return ''
    }
  },

  methods: {

    successEvent (file, response) {
      var image = {
        filename: file.upload.filename,
        size: file.size,
        width: file.width,
        height: file.height,
        type: file.type,
        url: response.mediaLink
      }

      axios.post('/api/v2/image', image)
      .then(response => {
        // this.updateImage(this.image.id, response.data.id)
      })
      .catch(e => {
        console.log(e)
      })
    },
    vqueueComplete (file, xhr, formData, image) {
      this.queueComplete = true
      this.$toast.open({
        message: 'Upload completo',
        type: 'is-success'
        // position: 'is-bottom'
      })

      // var task = {
      //   job: "['tile-orthomosaic']",
      //   process_status: 'waiting',
      //   project: this.orthomosaic.project,
      //   orthomosaic: this.orthomosaic.id
      // }

      // this.$store.dispatch('postTask', task).then((response) => {
      //   this.$toast.open({
      //     message: 'Tarefa criada com sucesso',
      //     type: 'is-success'
      //   })
      // })
      // .catch(e => {
      //   this.$toast.open({
      //     message: 'Erro ao criar a tarefa',
      //     type: 'is-danger'
      //   })
      // })
    },

    // updateImage: function (id, image) {
    //   return new Promise((resolve, reject) => {
    //     var imagem = {
    //       image: image
    //     }
    //     // trocar pelos mtodos criados
    //     this.$store.dispatch('putImage', imagem).then((response) => {
    //       this.image = response.data
    //       resolve(response.data)
    //       this.$toast.open({
    //         message: 'Imagem criada com sucesso',
    //         type: 'is-success'
    //       })
    //     })
    //     .catch(e => {
    //       this.$toast.open({
    //         message: 'Erro na criao do imagem',
    //         type: 'is-danger'
    //       })
    //     })
    //   })
    // },
    async validateBeforeSubmitImage () {
      if (this.$refs.fileUpload.getQueuedFiles() > 0) {
        // console.log(this.orthomosaic)
        await this.$store.dispatch('postImage', this.image).then((response) => {
          // console.log('ooooooo', response)
          this.image = response
        })
        this.$refs.fileUpload.processQueue()

        this.$toast.open({
          message: 'Upload em andamento',
          type: 'is-success'
        })
      } else {
        this.$toast.open({
          message: 'Imagem no adicionado',
          type: 'is-danger'
        })
      }
    }
  }
}
</script>

