<template>
  <section class="content">

    <div class="row center-block">
      <h2>Lista de Tarefas</h2>
      <div class="col-md-12">
        <div class="box">
          <!-- <div class="box-header">
            <h3 class="box-title">Data Table With Full Features</h3>
          </div> -->
          <!-- /.box-header -->
          <div class="box-body">
            <div class="dataTables_wrapper form-inline dt-bootstrap" id="example1_wrapper">
              <div class="row">
                <div class="col-sm-6">
                  <div id="example1_length" class="dataTables_length">

                  </div>
                </div>
              </div>

              <div class="row">
                <div class="col-sm-12 table-responsive">
                  <table aria-describedby="example1_info" role="grid" id="example1" class="table table-bordered table-striped dataTable">
                    <thead>
                      <tr role="row">
                        <th aria-label="Rendering engine: activate to sort column descending" aria-sort="ascending" style="width: 167px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting_asc">ID</th>

                        <th aria-label="Status de Processamento" style="width: 207px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Status de Processamento</th>

                        <th aria-label="Tarefa" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Tarefa</th>

                        <th aria-label="Inicio" style="width: 182px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Inicio</th>

                        <th aria-label="Fim" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Fim</th>

                        <th aria-label="Resultado" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Resultado</th>

                        <th aria-label="Projeto" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Projeto</th>

                        <th aria-label="Album" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Album</th>

                        <th aria-label="Ortomosaico" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0" class="sorting">Ortomosaico</th>

                        <th aria-label="Aes" style="width: 101px;" colspan="1" rowspan="1" aria-controls="example1" tabindex="0">Aes</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr class="even" role="row" v-for="(task, index) in tasks" :key="index">
                        <td class="sorting_1">{{ task.id }}</td>
                        <td>{{ task.process_status }}</td>
                        <td>{{ task.job }}</td>
                        <td>{{ task.start_time }}</td>
                        <td>{{ task.end_time }}</td>
                        <td>{{ task.output }}</td>
                        <td>
                          <div v-if="task.project">
                            {{ task.project.name }}
                          </div>
                        </td>
                        <td>
                          <div v-if="task.album">
                            {{ task.album.name }}
                          </div>
                        </td>
                        <td>
                          <div v-if="task.orthomosaic">
                            {{ task.orthomosaic.id }}
                          </div>
                        </td>
                        <td>
                          <div class="deleteButton" v-on:click.stop="confirmCustomDelete(task)">
                              <i class="fa fa-trash"></i>
                            </div>
                            <div class="editButton" v-on:click.stop="editTask(task)">
                              <i class="fa fa-edit"></i>
                            </div>
                            <!-- <div class="editButton" v-on:click.stop="execTask(task)">
                              <i class="fa fa-play"></i>
                            </div> -->
                            <div id="app">
                              <div id="modal" @click="showModal(task)"><i class="fa fa-play"></i></div>
                            </div>
                        </td>
                      </tr>
                    </tbody>
                    <tfoot>
                      <tr>
                        <th colspan="1" rowspan="1">ID</th>
                        <th colspan="1" rowspan="1">Status de Processamento</th>
                        <th colspan="1" rowspan="1">Tarefa</th>
                        <th colspan="1" rowspan="1">Inicio</th>
                        <th colspan="1" rowspan="1">Fim</th>
                        <th colspan="1" rowspan="1">Resultado</th>
                        <th colspan="1" rowspan="1">Projeto</th>
                        <th colspan="1" rowspan="1">Album</th>
                        <th colspan="1" rowspan="1">Ortomosaico</th>
                        <th colspan="1" rowspan="1">Aes</th>
                      </tr>
                    </tfoot>
                  </table>
                </div>
              </div>
            </div>
            <!-- /.box-body -->
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import $ from 'jquery'
import TaskView from './TaskView'
// Require needed datatables modules
require('datatables.net')
require('datatables.net-bs')

export default {
  name: 'list-tasks',
  data () {
    return {
      TaskComponent: TaskView,
      tasks: []
    }
  },
  mounted () {
    this.$store.dispatch('getTasks')
    .then(() => {
      this.tasks = this.$store.getters.getTasks

      this.$nextTick(() => {
        $('#example1').DataTable()
      })
    })
  },
  methods: {
    showModal (task) {
      this.$modal.open({parent: this,
        component: this.TaskComponent,
        hasModalCard: true,
        props: {
          task: task }
      })
    },

    editTask (task) {
      this.$router.push({name: 'EditTask', params: {id: task.id}})
    },
    execTask (task) {
      console.log(task)
      this.$store.dispatch('getRun', task)
      .then(() => {
        this.$toast.open({
          message: 'Tarefa executada!',
          type: 'is-success'
        })
      })
      .catch(() => {
        this.$toast.open({
          message: 'Tarefa no executada!',
          type: 'is-danger'
        })
      })
    },
    deleteProject (idTask) {
      this.$store.dispatch('deleteTask', idTask)
      .then(() => {
        this.$toast.open({
          message: 'Tarefa deletada!',
          type: 'is-success'
        })
      })
      // .catch(e => {
      //   console.log(e)
      // })
    },
    confirmCustomDelete (task) {
      this.$dialog.confirm({
        title: 'Deletando Projeto',
        message: 'Voc tem certeza que deseja <b>deletar</b> o album ? Isso  uma ao sem volta.',
        confirmText: 'Deletar projeto',
        type: 'is-danger',
        hasIcon: true,
        onConfirm: () => this.deleteProject(task.id)
      })
    }
  },
  components: {
    // 'va-box': VABox,
    // 'va-input': VAInput,
    // 'va-button': VAButton,
    TaskView
  }
}
</script>

<style>
/* Using the bootstrap style, but overriding the font to not draw in
   the Glyphicons Halflings font as an additional requirement for sorting icons.

   An alternative to the solution active below is to use the jquery style
   which uses images, but the color on the images does not match adminlte.

@import url('/static/js/plugins/datatables/jquery.dataTables.min.css');
*/

@import url('/static/js/plugins/datatables/dataTables.bootstrap.css');

table.dataTable thead .sorting:after,
table.dataTable thead .sorting_asc:after,
table.dataTable thead .sorting_desc:after {
  font-family: 'FontAwesome';
}

table.dataTable thead .sorting:after {
  content: '\f0dc';
}

table.dataTable thead .sorting_asc:after {
  content: '\f0dd';
}

table.dataTable thead .sorting_desc:after {
  content: '\f0de';
}
</style>



