import Vue from 'vue'
import Router from 'vue-router'
import Hello from 'components/Hello.vue'
import Sample from 'components/Sample.vue'
import DashboardV1 from 'examples/Dashboard.v1.vue'
import DashboardV2 from 'examples/Dashboard.v2.vue'
import InfoBoxExample from 'examples/InfoBoxExample'
import ChartExample from 'examples/ChartExample'
import AlertExample from 'examples/AlertExample'
import ModalExample from 'examples/ModalExample'
import WidgetsExample from 'examples/WidgetsExample'
import APIExample from 'examples/APIExample'

// UI Element Groups
import General from 'pages/ui-elements/General.vue'
import Icons from 'pages/ui-elements/Icons.vue'
import Buttons from 'pages/ui-elements/Buttons.vue'
import Sliders from 'pages/ui-elements/Sliders.vue'
import Timeline from 'pages/ui-elements/Timeline.vue'
import Modals from 'pages/ui-elements/Modals.vue'

// forms
import GeneralElements from 'pages/forms/GeneralElements.vue'
import AdvancedElements from 'pages/forms/AdvancedElements.vue'

import CreateProject from 'pages/forms/project/CreateProject.vue'
import ListProject from 'pages/forms/project/ListProject.vue'

import CreateSharing from 'pages/forms/sharing/CreateSharing.vue'
import ListSharing from 'pages/forms/sharing/ListSharing.vue'

import CreateEmail from 'pages/forms/email/CreateEmail.vue'
import ListEmails from 'pages/forms/email/ListEmails.vue'

import CreateTask from 'pages/forms/task/CreateTask.vue'
import ListTasks from 'pages/forms/task/ListTasks.vue'

import CreateOrthomosaic from 'pages/forms/orthomosaic/CreateOrthomosaic.vue'
import ListOrthomosaics from 'pages/forms/orthomosaic/ListOrthomosaics.vue'

import CreateUser from 'pages/forms/user/CreateUser.vue'
import ListUsers from 'pages/forms/user/ListUsers.vue'

import CreateImage from 'pages/forms/image/CreateImage.vue'
import ListImages from 'pages/forms/image/ListImages.vue'

import CreateAlbum from 'pages/forms/album/CreateAlbum.vue'
import ListAlbums from 'pages/forms/album/ListAlbums.vue'

import CreatePlan from 'pages/forms/plan/CreatePlan.vue'
import ListPlans from 'pages/forms/plan/ListPlans.vue'

// import CreateGeometry from 'pages/forms/geometry/CreateGeometry.vue'
// import ListGeometrys from 'pages/forms/geometry/ListGeometrys.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/sample',
      name: 'Sample',
      component: Sample
    },
    {
      path: '/dashboard/v1',
      name: 'DashboardV1',
      component: DashboardV1
    },
    {
      path: '/dashboard/v2',
      name: 'DashboardV2',
      component: DashboardV2
    },
    {
      path: '/examples/infobox',
      name: 'InfoBoxExample',
      component: InfoBoxExample
    },
    {
      path: '/examples/chart',
      name: 'ChartExample',
      component: ChartExample
    },
    {
      path: '/examples/alert',
      name: 'AlertExample',
      component: AlertExample
    },
    {
      path: '/examples/modal',
      name: 'ModalExample',
      component: ModalExample
    },
    {
      path: '/examples/widgets',
      name: 'WidgetsExample',
      component: WidgetsExample
    },
    {
      path: '/examples/api-example',
      name: 'APIExample',
      component: APIExample
    },
    {
      path: '/ui-elements/general',
      name: 'General',
      component: General
    },
    {
      path: '/ui-elements/icons',
      name: 'Icons',
      component: Icons
    },
    {
      path: '/ui-elements/buttons',
      name: 'Buttons',
      component: Buttons
    },
    {
      path: '/ui-elements/sliders',
      name: 'Sliders',
      component: Sliders
    },
    {
      path: '/ui-elements/timeline',
      name: 'Timeline',
      component: Timeline
    },
    {
      path: '/ui-elements/modals',
      name: 'Modals',
      component: Modals
    },
    {
      path: '/forms/general-elements',
      name: 'GeneralElements',
      component: GeneralElements
    },
    {
      path: '/forms/advanced-elements',
      name: 'AdvancedElements',
      component: AdvancedElements
    },
    {
      path: '/projects/create',
      name: 'CreateProject',
      component: CreateProject
    },
    {
      path: '/projects/list',
      name: 'ListProject',
      component: ListProject
    },
    {
      path: '/projects/edit/:id',
      name: 'EditProject',
      component: CreateProject,
      props: true
    },
    {
      path: '/plans/create',
      name: 'CreatePlan',
      component: CreatePlan
    },
    {
      path: '/plans/list',
      name: 'ListPlans',
      component: ListPlans
    }, {
      path: '/plans/edit/:id',
      name: 'EditPlan',
      component: CreatePlan,
      props: true
    },
    {
      path: '/sharing/create',
      name: 'CreateSharing',
      component: CreateSharing
    },
    {
      path: '/sharing/list',
      name: 'ListSharing',
      component: ListSharing
    },
    {
      path: '/sharing/edit/:id',
      name: 'EditSharing',
      component: CreateSharing,
      props: true
    },
    {
      path: '/email/create',
      name: 'CreateEmail',
      component: CreateEmail
    },
    {
      path: '/email/list',
      name: 'ListEmails',
      component: ListEmails
    },
    {
      path: '/tasks/create',
      name: 'CreateTask',
      component: CreateTask
    },
    {
      path: '/tasks/list',
      name: 'ListTasks',
      component: ListTasks
    },
    {
      path: '/tasks/edit/:id',
      name: 'EditTask',
      component: CreateTask,
      props: true
    },
    {
      path: '/orthomosaics/create',
      name: 'CreateOrthomosaic',
      component: CreateOrthomosaic
    },
    {
      path: '/orthomosaics/list',
      name: 'ListOrthomosaics',
      component: ListOrthomosaics
    },
    {
      path: '/users/create',
      name: 'CreateUser',
      component: CreateUser
    },
    {
      path: '/users/list',
      name: 'ListUsers',
      component: ListUsers
    },
    {
      path: '/images/create',
      name: 'CreateImage',
      component: CreateImage
    },
    {
      path: '/images/list',
      name: 'ListImages',
      component: ListImages
    },
    {
      path: '/album/create',
      name: 'CreateAlbum',
      component: CreateAlbum
    },
    {
      path: '/album/list',
      name: 'ListAlbums',
      component: ListAlbums
    },
    {
      path: '/album/edit/:id',
      name: 'EditAlbum',
      component: CreateAlbum,
      props: true
    }
    // {
    //   path: '/geometry/create',
    //   name: 'CreateGeometry',
    //   component: CreateGeometry
    // }, {
    //   path: '/geometry/list',
    //   name: 'ListGeometrys',
    //   component: ListGeometrys
    // }, {
    //   path: '/geometry/edit/:id',
    //   name: 'EditGeometry',
    //   component: CreateGeometry,
    //   props: true
    // }
  ],
  linkActiveClass: 'active'
})



